import { App } from 'vue';
//  babel-plugin-import;
// import 'ant-design-vue/lib/menu/style/css'
// import 'ant-design-vue/lib/dropdown/style/css'
import {
  Col,
  InputNumber,
  Divider,
  Button,
  Tooltip,
  Popover,
  Slider,
  Select,
  Switch,
  Radio,
  Row,
  Input,
  Modal,
  Dropdown,
  Menu,
  Checkbox,
  Drawer,
  Spin,
  Upload,
  Image,
  Table,
  Tabs,
  List
} from 'ant-design-vue';
// :
// :import 'ant-design-vue/dist/antd.css';
// : import 'ant-design-vue/lib/button/style';  ant-design-vue/lib/button/style/css  css 
const components = [
  Col,
  InputNumber,
  Divider,
  Button,
  Button.Group,
  Tooltip,
  Popover,
  Slider,
  Select,
  Select.Option,
  Select.OptGroup,
  Switch,
  Radio,
  Radio.Group,
  Radio.Button,
  Row,
  Input,
  Input.Group,
  Input.TextArea,
  Modal,
  Dropdown,
  Dropdown.Button,
  Menu,
  Menu.Item,
  Checkbox,
  Checkbox.Group,
  Drawer,
  Spin,
  Upload,
  Image,
  Table,
  Tabs,
  Tabs.TabPane,
  List,
  List.Item
];

export default function (app: App): void {
  for (let component of components) {
    // AInputNumberInputNumber
    app.component(component.name.slice(1), component);
  }
}
