export const base64toFile = (data: string, fileName: string) => {
  let arr = data.split(','),
    mineArr = arr[0].match(/:(.*?);/) as string[],
    mine = mineArr[1],
    // 
    suffix = mine.split('/')[1],
    // atob()  base-64 
    bstr = atob(arr[1]),
    // 
    n = bstr.length,
    // Uint8Array  8  0
    u8arr = new Uint8Array(n);
  // charCodeAt() Unicode ; 0  65535 
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  // new File(bits,name[,options])File,
  // btisArrayBufferArrayBufferViewBlob DOMString  Array
  // name ,
  // options, type MIME  ""
  return new File([u8arr], `fileName.${suffix}`, { type: mine });
};
/**
 * @description: base64Blob
 * @param {string} data base-64
 * @return {*} Blob
 */
export const base64toBlob = (data: string): Blob => {
  let arr = data.split(','),
    mineArr = arr[0].match(/:(.*?);/) as string[],
    mine = mineArr[1],
    suffix = mine.split('/')[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], { type: suffix });
};

/**
 * @description: blobfile
 * @param {Blob} blob {size,type}
 * @param {string} fileName 
 * @return {*} File
 */
export const blobToFile = (blob: Blob, fileName: string): File => {
  let { type } = blob;
  return new File([blob], `${fileName}.${type}`, { type: `${type}` });
};

/**
 * @description: 
 * @param {Blob} fileStream 
 * @param {string} filename 
 * @return {*}
 */
export function fileStreamDownload(fileStream: Blob | File, filename: string) {
  const elink = document.createElement('a'); // a
  elink.download = filename; // 
  elink.style.display = 'none';
  elink.href = URL.createObjectURL(fileStream); // URL
  document.body.appendChild(elink);
  elink.click();

  URL.revokeObjectURL(elink.href);
  document.body.removeChild(elink);
}
